<template>
	<div class="label">
		<similarHeader title='所有标签' @goBack='goBack' :haveMore='false'>
			<span>新建</span>
		</similarHeader>
		<div class="desc">	
			<p>暂无标签</p>
			<span>你可以通过给朋友添加标签来进行分类</span>
		</div>
		<div class="btn">	
			<van-button type="warning">新建标签</van-button>
		</div>
	</div>
</template>
<script>
	import {mixinTest1} from '@/mixin'
	import  similarHeader from '@/components/similar-header'
	export default{
		name:'label',
		mixins:[mixinTest1],
		components:{
			similarHeader
		}

	}
</script>
<style scoped>
	.label{
		height: 100vh;
		background: #ededed;
	}
	.desc{
		text-align: center;
		color:#969696;
		margin-top: 2rem;
	}
	header>span{
		font-size: .39rem;
	    position: absolute;
	    right: .2rem;
	    top: 50%;
	    transform: translateY(-50%);
	}
	.desc p{
		font-size: .4rem;
	}
	.desc span{
		font-size: .35rem;
	}
	.btn{
		text-align: center;
		margin-top: 4rem;
	}
	.btn >>> .van-button--warning {
		background: #0cbf16;
		border:1px solid #0cbf16;
		width: 90vw;
		border-radius: .13rem;
		font-size: .4rem;
	}
</style>


